import { connect } from "react-redux";

import { Button, Menu, MenuItem } from "@blueprintjs/core";
import { Popover2 } from "@blueprintjs/popover2";

import { RootState, AppDispatch } from '../../app/Store';
import { logOut } from "./AuthenticationSlice";

const mapState = (state: RootState) => ({
    isAuthenticated: state.authentication.isAuthenticated,
    user: {
      name: state.authentication.profile?.getGivenName() || "unknown",
      imageUrl: state.authentication.profile?.getImageUrl() || ""
    }
});

const mapDispatch = (dispatch: AppDispatch) => ({
    logOut: () => dispatch(logOut())
});

type LoggedInUserButtonProperties = ReturnType<typeof mapState> & ReturnType<typeof mapDispatch> & { }

const LoggedInUserButton = (properties: LoggedInUserButtonProperties) => {
    if (!properties.isAuthenticated) {
        return <div />;
    }
    
    const userImage = <img className="rounded" style={{ width: '25px', height: '25px' }} src={properties.user.imageUrl} alt="" />;

    const userMenu = (
      <Menu>
        <MenuItem icon="log-out" onClick={properties.logOut} text="Log Out" />
      </Menu>
    );

    return (
      <Popover2
        content={userMenu}
        interactionKind="click"
        placement="bottom-end"
        minimal={true}>
          <Button style={{ margin: 0}} className="bp3-minimal" icon={userImage} text={properties.user.name} />
      </Popover2>
    );
};

export default connect(mapState, mapDispatch)(LoggedInUserButton);
