import { connect } from 'react-redux';

import { Card, Elevation } from '@blueprintjs/core';
import { GoogleLogin, GoogleLoginResponse, GoogleLoginResponseOffline } from 'react-google-login';

import { RootState, AppDispatch } from '../../app/Store';
import { logIn } from './AuthenticationSlice';

function isGoogleLoginResponse(response: GoogleLoginResponse | GoogleLoginResponseOffline): response is GoogleLoginResponse {
  return (response as GoogleLoginResponse).getBasicProfile !== undefined;
}

const mapState = (state: RootState) => ({
    isAuthenticated: state.authentication.isAuthenticated
});

const mapDispatch = (dispatch: AppDispatch) => ({
    logIn: (response: GoogleLoginResponse) => dispatch(logIn(response))
});

type LoginCardProperties = ReturnType<typeof mapState> & ReturnType<typeof mapDispatch> & { }

const LoginCard = (properties: LoginCardProperties) => {
    const clientId = "279418559001-t7d7c0hmaii6sd2ob4a87at6qujlmfb9.apps.googleusercontent.com";

    function onSuccess(response: GoogleLoginResponse | GoogleLoginResponseOffline) {
      if (isGoogleLoginResponse(response)) {
        properties.logIn(response);
      }
    }

    function onFailure(result: any) {
      console.log("Login Failed", result);
    }

    return (
      <div className='loginCardContainer'>
        <Card elevation={Elevation.THREE}>
          <h2>Sign In</h2>
          <p>Authentication is required to use this application.</p>

          <div className="buttonContainer">
            <GoogleLogin
              clientId={clientId}
              buttonText='Authenticate With Google'
              onSuccess={onSuccess}
              onFailure={onFailure}
              cookiePolicy={'single_host_origin'}
              isSignedIn={true} />
            </div>
        </Card>
      </div>
    );
};

export default connect(mapState, mapDispatch)(LoginCard);
