import { ReactElement, useEffect, useState } from "react";

import { Tabs, TabId } from "@blueprintjs/core";
import { useNavigate } from "react-router-dom";

export default function NavigatingTabs({ name, defaultTab, children }: { name: string, defaultTab: TabId, children: ReactElement[] }) {
    const navigate = useNavigate();
    const [ selectedTabId, setSelectedTabId ] = useState(defaultTab as TabId);

    useEffect(
        () => navigate(selectedTabId.toString()),
        [ navigate, selectedTabId ]
    );

    return (
      <Tabs id={name} onChange={setSelectedTabId} selectedTabId={selectedTabId}>
        {children}
      </Tabs>
    );
}
