import { Routes, Route, Outlet } from "react-router-dom";
import { Tab } from "@blueprintjs/core";
import NavigatingTabs from '../components/NavigatingTabs'

import NotFound from './NotFound'

export default function Review() {
    return (
      <Routes>
        <Route path="/" element={<Layout />}>
          <Route path="balances" element={<div>TBD - Balances</div>} />
          <Route path="spending" element={<div>TBD - Spending</div>} />
          <Route path="monthly-clearing" element={<div>TBD - Monthly Clearing</div>} />
          <Route path="quarterly-review" element={<div>TBD - Quarterly Review</div>} />
          <Route path="*" element={<NotFound />} />
        </Route>
      </Routes>
    )
}

function Layout() {
    return (
      <NavigatingTabs name="Review" defaultTab="balances">
        <Tab id="balances" title="Balances" panel={<Outlet />} />
        <Tab id="spending" title="Spending" panel={<Outlet />} />
        <Tab id="monthly-clearing" title="Monthly Clearing" panel={<Outlet />} />
        <Tab id="quarterly-review" title="Quarterly Review" panel={<Outlet />} />
      </NavigatingTabs>
    );
}
