import { connect } from 'react-redux';
import { Card, Elevation } from '@blueprintjs/core';
import { AppDispatch, RootState } from '../../app/Store';
import { UserId } from './CreditScoresSlice';

// TODO: Don't hard-code names
const mapState = (state: RootState) => ({
    markCreditScore: state.creditScores.scores.get(UserId.Mark),
    ivyCreditScore: state.creditScores.scores.get(UserId.Ivy)
});

const mapDispatch = (dispatch: AppDispatch) => ({});

type CreditScoresProperties = ReturnType<typeof mapState> & ReturnType<typeof mapDispatch> & { }

const CreditScores = (properties: CreditScoresProperties) => {
    return (
      <Card elevation={Elevation.TWO}>
        <h1>Credit Scores</h1>
        <p>Mark: {properties.markCreditScore?.score} (Last Updated {properties.markCreditScore?.timestamp.toDateString()})</p>
        <p>Ivy: {properties.ivyCreditScore?.score} (Last Updated {properties.ivyCreditScore?.timestamp.toDateString()})</p>
      </Card>
    );
};


export default connect(mapState, mapDispatch)(CreditScores);
