import { Routes, Route, Outlet } from "react-router-dom";
import { Tab } from "@blueprintjs/core";
import NavigatingTabs from '../components/NavigatingTabs'

import NotFound from './NotFound'

export default function Planning() {
    return (
      <Routes>
        <Route path="/" element={<Layout />}>
          <Route path="goals" element={<div>TBD - Goals </div>} />
          <Route path="large-purchases" element={<div>TBD - Large Purchases</div>} />
          <Route path="budgets" element={<div>TBD - Budgets</div>} />
          <Route path="*" element={<NotFound />} />
        </Route>
      </Routes>
    )
}

function Layout() {
    return (
      <NavigatingTabs name="Planning" defaultTab="goals">
        <Tab id="goals" title="Goals" panel={<Outlet />} />
        <Tab id="large-purchases" title="Large Purchases" panel={<Outlet />} />
        <Tab id="budgets" title="Budgets" panel={<Outlet />} />
      </NavigatingTabs>
    );
}
