import './App.css';

import { connect } from 'react-redux';
import { Outlet, useNavigate } from "react-router-dom";
import { Button, Navbar, IconName, Alignment } from "@blueprintjs/core";

import UserMenuButton from './features/authentication/UserMenuButton';
import LoginCard from './features/authentication/LoginCard';
import { RootState, AppDispatch } from './app/Store';

const mapState = (state: RootState) => ({
    isAuthenticated: state.authentication.isAuthenticated
});

const mapDispatch = (dispatch: AppDispatch) => ({});

type AppProperties = ReturnType<typeof mapState> & ReturnType<typeof mapDispatch> & { }

const App = (properties: AppProperties) => {
  const navigate = useNavigate();

  function createButton(name: string, icon: IconName, target: string) {
    return <Button key={name} className="bp3-minimal" icon={icon} text={name} disabled={!properties.isAuthenticated} onClick={() => navigate(target)}/>
  }

  const navigationButtons = [
    createButton("Overview", "home", "/"),
    createButton("Data", "database", "/data"),
    createButton("Planning", "chart", "/planning"),
    createButton("Review", "endorsed", "/review")
  ]

  return (
    <div className="App">
      <Navbar className="bp3-dark">
        <Navbar.Group>
          <Navbar.Heading>Personal Finance</Navbar.Heading>
          <Navbar.Divider />
          {navigationButtons}
        </Navbar.Group>

        <Navbar.Group align={Alignment.RIGHT}>
          <UserMenuButton />
        </Navbar.Group>
      </Navbar>

      <div style={{ padding: "0 50px" }}>
        { properties.isAuthenticated ? <Outlet /> : <LoginCard /> }
      </div>
    </div>
  );
}

export default connect(mapState, mapDispatch)(App);
